import React, { Component} from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import  SidebarAgent from './SidebarAgent';
import axios from 'axios';
import { KONEKSI } from '../../support/config';
import Cookies from "universal-cookie";
const cookies = new Cookies();
class OrderAgent extends Component {
    state={
        listOrder:[]
    }
    componentDidMount(){
        this.getOrder()
    }
    getOrder=()=>{
        var username = cookies.get('myPengguna');
        axios.get(`${KONEKSI}/transaction/getlistorder/${username}`
        ).then((res) => {
            this.setState({ listOrder: res.data});
        }).catch((err) => {
            console.log(err);
        })
    }
    putData=()=>{
        const data = this.state.listOrder.map((item)=>{
            if(item.is_finished == 'no'){
                return(
                    <tr key={item.id_customer} className="text-wrap" style={{fontSize:'12px'}}>  
                        <td className="align-middle">{item.id_transaksi}</td>  
                        <td className="align-middle">{item.total_berat}</td>  
                        <td className="align-middle">{item.total_bayar}</td>  
                        <td className="align-middle">{item.waktu}</td>  
                        <td className="align-middle">Belum dibayar</td>                     
                    </tr>
                    )
            }else{
                return(
                    <tr key={item.id_customer} className="text-wrap" style={{fontSize:'12px'}}>  
                        <td className="align-middle">{item.id_transaksi}</td>  
                        <td className="align-middle">{item.total_berat}</td>  
                        <td className="align-middle">{item.total_bayar}</td>  
                        <td className="align-middle">{item.waktu}</td>  
                        <td className="align-middle">{item.is_finished}</td>                     
                    </tr>
                    )
            }
            
        })
        return data
    }

    render(){
        const { username } = this.props.user;

        if(username !== ""){
            return (
                <div className="container-fluid">
                    <div className="row">
                        <SidebarAgent />

                        <div className="col-md-10 bg-light pl-3 pt-3">
                                <div className="alert alert-warning media col-12">
                                    <img className="img img-fluid" src="http://localhost:3000/images/flat/039-stadistics.png" width="100px" />
                                    <div className="col-md-10 media-body">
                                        <h4>Your order here</h4>
                                        <p> Lorem, ipsum dolor sit amet consectetur adipisicing elit. Beatae, cupiditate minima similique quaerat nulla iusto dolorem quam asperiores ratione ex tempore in nemo harum consequatur fuga necessitatibus voluptatem sint dolor. </p>
                                    </div>
                                </div>  
                                <div className="card mt-2 shadow col-lg-12 pl-0 pr-0">
                                    <div className="card-header">
                                        <h4>Keranjang Belanja customermu</h4>
                                    </div>
                                    <div className="card-body">
                                        <table className="table table-hover">
                                            <thead>
                                                <tr>
                                                    <th scope="col">Nama transaksi</th>
                                                    <th scope="col">Total berat</th>
                                                    <th scope="col">Total Pembayaran</th>
                                                    <th scope="col">Tanggal</th>
                                                    <th scope="col">Status</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                            {this.putData()}
                                            </tbody>
                                        </table>
                                    </div>
                                </div>                          
                        </div>
                    
                                
                    </div>

                </div>
            );
        }
        return(
            <Redirect to="/" />
        );
    }
}

const mapStateToProps = (state) => {
    return { 
        user: state.auth
    };
}

export default connect(mapStateToProps)(OrderAgent);