import React, { Component } from 'react';
import axios from 'axios';
import { connect } from 'react-redux'
import { KONEKSI } from '../support/config';
import { select_produk } from '../actions'
import { loadOfCart } from '../actions';
import BannerKu from './BannerKu';
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
class ProductDetailKu extends Component {
    constructor(props) {
        super(props);
    
        this.toggle = this.toggle.bind(this);
        this.state = {
          dropdownOpen: false,
          customer:0,
          listCustomer:[],
          namaCostumer:'Pilih customer'
        };
      }
    
      toggle() {
        this.setState(prevState => ({
          dropdownOpen: !prevState.dropdownOpen
        }));
      }
    componentDidMount(){
        this.getListUser()
        var id = this.props.match.params.id;
        console.log(id)

        axios.get(`${KONEKSI}/product/getproductdetail/${id}`)
        .then((res) => {
            console.log(res);
            this.props.select_produk(res.data[0])
        }).catch((err) => {
            console.log(err)
        })
    }

    onAddToCart = () => {
        const { id, nama_produk, harga, berat } = this.props.produk;
        var id_customer = this.state.customer;
        var username = this.state.namaCostumer;
        var id_agent = this.props.user.id_agent
        var jumlah_beli = parseInt(this.refs.tbJumlah.value);
        var total_berat = parseInt(berat * jumlah_beli);
        var total_harga = harga * jumlah_beli;
        var id_produk = id;
        console.log(username, id, nama_produk, harga, berat, jumlah_beli, total_harga);

        axios.post(`${KONEKSI}/transaction/addtocart`, {
            id_customer,
            id_agent,
            id_produk,
            nama_produk,
            harga,
            berat,
            jumlah_beli,
            total_berat,
            total_harga
        }).then((res) => {
            console.log(res);
            alert(`produk ${nama_produk} \nberhasil ditambah ke cart sejumlah: ${jumlah_beli} untuk costumer: ${username}`)
            this.props.loadOfCart({id_customer})
        }).catch((err) => {
            alert(err);
        })
    }

    renderButtonBuy = () => {
        if (this.props.username === ""){
            return <button className="btn btn-success col-md-11 disabled"><i class="fas fa-exclamation-triangle"></i> Masuk untuk Membeli!</button>
        }else if(this.props.level !== null && this.state.customer != 'Pilih customer'){
            return <button onClick={this.onAddToCart} className="btn btn-success col-md-11"><i class="fas fa-cogs"></i> Tambah Ke Dashboard</button>
        }else if(this.props.level === null){
            return <a href='/verify'><button className="btn btn-success col-md-11 disabled"><i class="fas fa-exclamation-triangle"></i>Berlangganan sebelum membeli</button></a>
        }else{
            return <button  className="btn btn-danger col-md-11"><i class="fas fa-danger"></i>Pilih customer terlebih dahulu</button>
        }
       
      
    }
    getListUser =()=>{
        axios.get(`${KONEKSI}/customer/getcustomer/${this.props.user.id_agent}`
        ).then((res) => {
            this.setState({listCustomer: res.data}); 
            console.log(this.state.listCustomer)  
        }).catch((err) => {
            console.log(err);
        })
    }
    listUser=()=>{
        const userList = this.state.listCustomer.map((item)=>{
            return(
               
                    <DropdownItem onClick={()=>this.setState({customer:item.id_customer, namaCostumer:item.nama_lengkap})}>{item.nama_lengkap}</DropdownItem>
                
            )
        })
        return userList
    }
    render() {
        const {  harga, berat, deskripsi, gambar,nama_produk,negara,kategori} = this.props.produk;
        const sumber = `${KONEKSI}/${gambar}`;
        if(this.props.username !== ''){
        return(
            <div>
                <div className="row bg-light" style={{margin:"10px"}}>
                    <div className="col-md-3 offset-md-1" style={{display: "block"}}>
                        <img className="card-img-top" src={sumber}  className="ml-auto" alt={nama_produk} style={{marginTop: "10px", minWidth:'280px', maxHeight:"400px", maxWidth:"400px", paddingLeft:0}} />
                    </div>
                    <div className="col-md-4 ml-4">
                        <div className="alert" style={{textAlign:"left"}}>
                            
                           
                           
                           

                            <div style={{fontSize:"14px"}}>
                                <h3>{nama_produk}</h3>
                                <h5 className="text-danger">Rp. {harga.toLocaleString()}</h5>
                                <div class="dropdown-divider"></div>
                                <div className="row">
                                    <div className="col-md-5">Kategori:</div>
                                    <div className="col-md-7"><p>{kategori}</p></div>
                                </div>
                                <div className="row">
                                    <div className="col-md-5">Negara:</div>
                                    <div className="col-md-7"><p>{negara}</p></div>
                                </div>
                                <div className="row">
                                    <div className="col-md-5">Berat:</div>
                                    <div className="col-md-7"><p>{berat} gr</p></div>
                                </div>
                                
                                <div className="row">
                                    <div className="col-md-5 pt-1">Jumlah:</div>
                                    <div className="col-md-7 mb-3">
                                        <input type="number" min={1} className="form-control col-md-6 text-center" style={{padding: "0 12px"}} placeholder="Jumlah" ref="tbJumlah" defaultValue="1" />
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-5">Pilih Customer:</div>
                                    <div className="col-md-7 mb-3">
                                    <Dropdown isOpen={this.state.dropdownOpen} toggle={this.toggle}>
                                        <DropdownToggle caret>
                                        {this.state.namaCostumer}
                                        </DropdownToggle>
                                        <DropdownMenu>
                                        {this.listUser()}
                                        </DropdownMenu>
                                    </Dropdown>
                                    </div>
                                </div>
                            </div>
                            {this.renderButtonBuy()}
                        </div>
                    </div>

                    <div className="col-md-3" style={{ textAlign:"left"}}>
                        <BannerKu />
                    </div>
                </div>
                {/* END ROW #1 */}

                <div className="row" style={{margin: "25px"}}>
                    <div className="col-md-10 offset-md-1 card" style={{padding:0, textAlign:"left"}}>
                    
                        <h4 className="card-header">DESKRIPSI</h4>
                        
                        <div className="card-body">
                            <p>{deskripsi}</p>
                        </div>
                    </div>
                </div>
            </div>
        );
        }
        return(
            <div>
                <div className="row bg-light" style={{margin:"10px"}}>
                    <div className="col-md-3 offset-md-1" style={{display: "block"}}>
                        <img className="card-img-top" src={sumber}  className="ml-auto" alt={nama_produk} style={{marginTop: "10px", minWidth:'280px', maxHeight:"400px", maxWidth:"400px", paddingLeft:0}} />
                    </div>
                    <div className="col-md-4 ml-4">
                        <div className="alert" style={{textAlign:"left"}}>
                            
                           
                            {/* <h5 className="text-danger">Rp. {harga.toLocaleString()}</h5> */}
                            <div class="dropdown-divider"></div>

                            <div style={{fontSize:"14px"}}>
                            <h3>{nama_produk}</h3>
                                <div class="dropdown-divider"></div>
                                <div className="row">
                                    <div className="col-md-5">Kategori:</div>
                                    <div className="col-md-7"><p>{kategori}</p></div>
                                </div>
                                <div className="row">
                                    <div className="col-md-5">Negara:</div>
                                    <div className="col-md-7"><p>{negara}</p></div>
                                </div>
                                <div className="row">
                                    <div className="col-md-5">Berat:</div>
                                    <div className="col-md-7"><p>{berat} gr</p></div>
                                </div>
                                
                                <div className="row">
                                    <div className="col-md-5 pt-1">Jumlah:</div>
                                    <div className="col-md-7 mb-3">
                                        <input type="number" min={1} className="form-control col-md-6 text-center" style={{padding: "0 12px"}} placeholder="Jumlah" ref="tbJumlah" defaultValue="1" />
                                    </div>
                                </div>
                            </div>
                            {this.renderButtonBuy()}
                        </div>
                    </div>

                    <div className="col-md-3" style={{ textAlign:"left"}}>
                        <BannerKu />
                    </div>
                </div>
                {/* END ROW #1 */}

                <div className="row" style={{margin: "25px"}}>
                    <div className="col-md-10 offset-md-1 card" style={{padding:0, textAlign:"left"}}>
                    
                        <h4 className="card-header">DESKRIPSI</h4>
                        
                        <div className="card-body">
                            <p>{deskripsi}</p>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return { 
        produk: state.selectedProduk,
        load: state.loadOfCart,
        username: state.auth.username,
        user: state.auth,
        level:state.auth.level
    };
}

export default connect(mapStateToProps, { select_produk, loadOfCart })(ProductDetailKu);